import React from 'react';
import MapComponent from "../components/MapComponent";
import Fade from 'react-reveal/Fade';


export default function MapContainer() {
    return (
        <div>
            <Fade bottom>
                <div className="titleDiv">
                    찾아오시는 길
                </div>
                <MapComponent/>
            </Fade>
        </div>
    );
}